* {
  box-sizing: border-box;
}

body {
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header"
                       "main"
                       "footer";
  min-height: 100vh;
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Helvetica Neue, Arial, sans-serif;
  display: grid;
}

header {
  color: #fff;
  background-color: #000;
  grid-area: header;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 3rem;
  padding: 0 2rem;
  display: flex;
}

main {
  grid-area: main;
  padding: 1rem;
}

footer {
  color: #fff;
  background-color: #000;
  grid-area: footer;
  justify-content: center;
  align-items: center;
  margin: 3rem 0 0;
  padding: 1rem;
  display: flex;
}

header a, footer a {
  color: orange;
  font-weight: bold;
  text-decoration: none;
}

header a:hover, header a:active, footer a:hover, footer a:active {
  color: #ff0;
}

footer ul {
  justify-content: center;
  gap: 3rem;
  list-style: none;
  display: flex;
}

#search-summary {
  text-align: center;
  margin: 0 .5rem 1rem;
  font-size: .8em;
}

#productlist {
  flex-flow: wrap;
  justify-content: center;
  align-items: stretch;
  gap: 3rem 1.5rem;
  display: flex;
}

#productlist article {
  background-color: #d3d3d3;
  flex-direction: column;
  grid-template-rows: auto auto 1fr auto;
  grid-template-areas: "category"
                       "image"
                       "info"
                       "buyform";
  justify-content: space-between;
  max-width: 300px;
  font-size: .9em;
  display: grid;
  overflow: hidden;
  box-shadow: 3px 3px 8px #00000026;
}

#productlist article .info-wrapper {
  grid-area: info;
  padding: 1.2rem;
}

#productlist article .info-wrapper > div {
  text-align: center;
}

#productlist article .info-wrapper .description {
  text-align: left;
  margin: 1.2rem 0;
}

.label {
  font-weight: 700;
}

#productlist article .stock.alert {
  color: #8b0000;
}

#productlist article .stock.alert:after {
  content: "nearly sold out!";
  color: #fff;
  background-color: #8b0000;
  border-radius: 10px;
  margin-left: .5rem;
  padding: 0 .4rem .2rem;
}

#productlist article .price {
  text-align: right;
  font-size: 2.5em;
}

#productlist article .discountPercentage {
  text-align: right;
}

#productlist article .price:before {
  content: "$";
}

#productlist article .discountPercentage:after {
  content: "% off!";
}

#productlist article h3 {
  text-align: center;
  margin: 0;
}

#productlist article .brand {
  text-align: center;
  margin-bottom: 1rem;
  font-style: italic;
}

#productlist article .image-wrapper {
  color: #fff;
  background-color: #858585;
  grid-area: image;
  justify-content: center;
  align-items: flex-start;
  height: 300px;
  display: flex;
  overflow: hidden;
}

#productlist article .category {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #000;
  grid-area: category;
  font-size: .9em;
  font-weight: 700;
}

#productlist article img {
  object-fit: cover;
  width: 100%;
  max-width: 100%;
  height: 100%;
  display: block;
}

form.buy-product {
  grid-area: buyform;
  justify-content: center;
  display: flex;
}

form.buy-product button {
  color: #000;
  cursor: pointer;
  background-color: #7595d1;
  border: 0;
  border-radius: 0 15px 0 0;
  outline: 0;
  padding: .5rem;
}

form.buy-product button:focus, form.buy-product button:active, form.buy-product button:hover {
  background-color: orange;
  box-shadow: 0 0 10px orange;
}

form.buy-product input {
  color: #fff;
  background-color: #000;
  border: 0;
  border-radius: 15px 0 0;
  outline: 0;
  padding: .5rem .7rem;
}

#productlist article .rating {
  flex-direction: row;
  justify-content: center;
  gap: .5rem;
  font-size: .9em;
  display: flex;
}

#points-template {
  width: 0;
  height: 0;
}

.points {
  width: 20px;
  height: 20px;
}

.points:not(.scored) {
  fill: #0000;
  stroke: #a9a9a9;
}

.points.scored {
  opacity: 1;
  fill: #8a2be2;
}
